@import '../../stylemodules/main.scss';

@mixin headermode($border, $bgcolor, $fontColor, $logoColor) {
    width: 100%;
    height: 71.89px;
    background: $bgcolor;
    position: fixed;
    top: 0;
    z-index: 9999;
    border-bottom: $border;
    transition: all 0.3s;
    
    .header-inner {
        width: 80%;
        height: 100%;
        margin: 0 auto;
        max-width: 1300px;
        min-width: 325px;
        justify-content: space-between;
        padding: 0px;
        display: flex;
        align-items: center;
        @include responsive(T) {
            flex-direction:column;
        }
        .header-logo-container {
            
            width: 100%;
            display: flex;
            height: 71px;
            justify-content: space-between;
            align-items: center;
            .header-menu-mobile {
                font-size:1.8rem;
             
            }
        }
      
        .header-menu-mobile {
            display:none;
            @include responsive(T) {
                display:block;
            }
        }
      
        .header-logo {
            width: 130px;
            height: 40px;
            @include responsive(T) {
                width:110px;
                height:35px;
            }
            @if $logoColor ==W {
                background: url('../../asset//images/logo_negative.png') center center no-repeat;
            }

            @if $logoColor ==T {
                background: url('../../asset//images/logo_white.png') center center no-repeat;
            }

            background-size: contain;
            background-repeat:no-repeat;
        }

        .header-menu {
            color: $fontColor;
            margin: 0;
            padding: 0;
            list-style: none;
            z-index: -1;
            display:flex;
            width:100%;
            
            @include responsive(T) {
               position:absolute;
               top:-320px;
               flex-direction:column;
               background:#ffffff;
               transition:all 0.3s;
            }
            
            li {
                @content;
                margin-left: 10px;
                padding: 5px;
                cursor: pointer;
                font-weight: 700;
                border-radius: 10px;
                font-size: 15.82px;
                padding:10px;
                width:80px;
                @include responsive(T) {
                    padding:15px;
                    width:auto;
                }
                @include responsive(T) {
                    font-size:13.82px;
                }
            }

            li:hover {
                color: #000000
            }
        }
        .header-menu.active {
            top:73px;
            box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
            padding:30px 0;
        }
    }
}

header.scrolled {
    @include headermode(1px solid #ddd, #ffffff, #000000, W);
}

header.default {
    @include headermode(none, none, #ffffff, T);
}

header.default:hover {
    @include headermode(1px solid #ddd, #ffffff, #000000, W) {
        color: gray;
    };
}