@import '../../../stylemodules/main.scss';
.section07-container {
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;

    .section07-txt-container {
        .section07-sub-txt {
            font-family: 'Spoqa Han Sans Neo';
            font-style: normal;
            font-weight: 700;
            font-size: 1rem;
            color: #949494;
            @include responsive(T) {
                font-size: $md-font-size-tablet;
            }

            @include responsive(M-L) {
                font-size: $md-font-size-mobile;
            }

            @include responsive(M-S) {
                font-size: $sm-font-size-mobile;
            }
        }

        .section07-head-txt {

            font-family: 'Poppins';
            font-style: normal;
            font-weight: 900;
            font-size: 2.5rem;

            /* identical to box height, or 133% */


            color: #5B5B5B;

            @include responsive(T) {
                font-size: $lg-font-size-tablet;
            }

            @include responsive(M-L) {
                font-size: $lg-font-size-tablet;
            }

            @include responsive(M-S) {
                font-size: $lg-font-size-mobile;
            }
        }

        .section07-description {
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 300;
            font-size:$sm-font-size;
            /* identical to box height, or 133% */
            color: #5B5B5B;

            @include responsive(T) {
                font-size: $md-font-size-tablet;
            }

            @include responsive(M-L) {
                font-size: $md-font-size-mobile;
            }

            @include responsive(M-S) {
                font-size: $sm-font-size-mobile;
            }
        }
    }
}