@import '../../../stylemodules/main.scss';
.section13-container {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-around;
    align-items: center;
    padding:120px 0;
    @include responsive(M-L) {
       padding:80px 0;
    }
    .section13-txt-container {
        border-bottom: 1px solid #FFFFFF;
        width:80%;
        max-width:700px;
        min-width:325px;
        margin:0 auto;
        text-align:center;
        h1 {
            font-family: 'Poppins';
            font-style: normal;
            font-weight: 600;
            text-align:center;
            font-size: $lg-font-size;
            letter-spacing:1px;
            @include responsive(T) {
                font-size: $lg-font-size-tablet;
            }
            @include responsive(M-L) {
                font-size: $lg-font-size-tablet;
            }
            @include responsive(M-S) {
                font-size: $lg-font-size-tablet;
            }
          
            /* identical to box height */
            color: #FFFFFF;
            margin-bottom:30px;
        }

        h4 {
            font-family: 'Noto Sans KR';
            font-style: normal;
            font-weight: 300;
            font-size: 1.2rem;
            margin-bottom:30px;
            color: #FFFFFF;
            word-break: keep-all;

            width:100%;

            @include responsive(T) {
                font-size: $sm-font-size-tablet;
            }
            @include responsive(M-L) {
                font-size: $sm-font-size-tablet;
            }
            @include responsive(M-S) {
                font-size: $sm-font-size-mobile;
            }
          
        }
        
    }
    span {
        margin-top:30px;
        background: #FFFFFF;
        padding:8px 24px;
        font-size:0.9rem;
        font-weight:300;
        color:#767676;
        cursor:pointer;
        width:40%;
    }
}