@import '../../../stylemodules/main.scss';
.section08-container {
    padding:120px 0;
    @include responsive(M-L) {
      padding:80px 0;
    }
    
    
    h4 {
        font-size:1.2rem;
        color:#949494;
        @include responsive(T) {
          font-size: $md-font-size-tablet;
      }

      @include responsive(M-L) {
          font-size: $md-font-size-mobile;
      }

      @include responsive(M-S) {
          font-size: $sm-font-size-mobile;
      }
    }
    h2 {
        padding-top:20px;
        font-size:2rem;
        @include responsive(T) {
          font-size: $lg-font-size-tablet;
      }

      @include responsive(M-L) {
          font-size: $lg-font-size-tablet;
      }

      @include responsive(M-S) {
          font-size: $lg-font-size-mobile;
      }
    }
 
   .section08_img {
    width:90%;
    height:500px;
    margin:0 auto;
    background: url('../../../asset/images/section08_img.png') center center no-repeat;
    background-size: contain;

    @include responsive(T) {
       
    }

    @include responsive(M-L) {
      
    }

    @include responsive(M-S) {
       
    }

 

    @include responsive(D) {
      width:100%; 
    }
   }

    
}